<template>
  <div>
    <section class="reviews-block1">
      <div class="back" v-if="this.$vssWidth > 1170">
          <VueSlickCarousel v-if="!loading" v-bind="reviews2" ref="reviews2" class="reviews-slider2">
            <div class="img" v-for="review in imageReviews" :key="review.id">
              <img :src="getSrc(review.thumb)" alt="">
            </div>
          </VueSlickCarousel>
      </div>
      <div class="container">
        <h2 class="section__title">Отзывы наших любимых клиентов</h2>
        <div class="block">
          <div class="flex-start align-center">
            <div class="prev carousel-btn" @click="prevReview">
              <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M6.32959 1.30876L1.66292 5.97542L6.32959 10.6421" stroke="#22252C" stroke-width="1.5"
                      stroke-linecap="round" stroke-linejoin="round"/>
              </svg>
            </div>
            <div class="next carousel-btn" @click="nextReview">
              <svg width="8" height="12" viewBox="0 0 8 12" fill="none" xmlns="http://www.w3.org/2000/svg">
                <path d="M1.6626 1.30876L6.32926 5.97542L1.6626 10.6421" stroke="#22252C" stroke-width="1.5"
                      stroke-linecap="round" stroke-linejoin="round"/>
              </svg>
            </div>
          </div>
          <div class="center">
            <img v-if="this.$vssWidth > 1170" src="/static/img/home/7/1.png" alt="" class="phone">
            <img v-if="this.$vssWidth <= 1170" src="/static/img/home/7/1-m.png" alt="" class="phone">
          <VueSlickCarousel v-if="!loading" v-bind="reviews1" ref="reviews1" class="reviews-slider1">
            <div class="img" v-for="review in imageReviews" :key="review.id">
              <img :src="getSrc(review.thumb)" alt="">
            </div>
          </VueSlickCarousel>
          </div>
        </div>

      </div>
    </section>
    <section class="reviews-block2">
      <img src="/static/img/reviews/bg.png" alt="" class="bg">
      <div class="container">
        <div class="grid">
          <VideoReview v-for="review in videoReviews" :key="review.id" :review="review"></VideoReview>
        </div>
      </div>
    </section>
  </div>
</template>

<script>
import VueSlickCarousel from 'vue-slick-carousel'
import 'vue-slick-carousel/dist/vue-slick-carousel.css'
import VideoReview from "@/components/Reviews/VideoReview";
import api from "@/api"
import {createImageUrl} from "@/utils";

export default {
  name: "ReviewsPage",
  components: {VueSlickCarousel, VideoReview},
  created() {
    this.getItems()
  },
  methods: {
    getSrc(src) {
      return createImageUrl(src)
    },
    async getItems() {
      this.loading = true
      const data = await api.getReviews(this.category)
      this.imageReviews = data.image_reviews
      this.videoReviews = data.video_reviews
      this.loading = false
    },
    nextReview() {
      this.$refs.reviews1.next()
      this.$refs.reviews2.next()
    },
    prevReview() {
      this.$refs.reviews1.prev()
      this.$refs.reviews2.prev()
    },
  },
  data: () => ({
    loading: false,
    imageReviews: [],
    videoReviews: [],
    reviews: [
      {
        id: 1,
        img: "/static/img/home/7/4.png"
      },
      {
        id: 1,
        img: "/static/img/home/7/4.png"
      },
      {
        id: 1,
        img: "/static/img/home/7/4.png"
      },
      {
        id: 1,
        img: "/static/img/home/7/4.png"
      },
    ],
    reviews1: {
      slidesToShow: 1,
      arrows: false,
      dots: false,
      touchMove: false,
      variableWidth: true,
      adaptiveHeight: true
    },
    reviews2: {
      arrows: false,
      dots: false,
      touchMove: false,
      variableWidth: true,
      adaptiveHeight: true,
      centerMode: true,
      infinite: true,
    },
  })
}
</script>

<style scoped>

</style>